interface OfferingDialogProps {
    name: OfferingName
    offering: Offerings
}

const OfferingDialog: FunctionComponent<OfferingDialogProps> = (props) => {

    return <DialogContent className="bg-[#d1c6ad]">
        <h4 className="text-5xl">{props.offering.title}</h4>
        {props.offering.details.map((detail, index) => <p
            key={index}
        >
            {detail}
        </p>)}
        <a
            target="_blank"
            href={`mailto:hello@copperdusk.com?subject=${startCase(props.offering.actionTitle)}`}
            className="text-center px-8 py-3 bg-[#448089] font-medium text-white text-md md:text-lg rounded-md hover:bg-slate-700 transition ease-in-out duration-300"
        >
            Let's talk about {props.offering.actionTitle}!
        </a>
    </DialogContent>
}

